.header{font-size:4rem;margin:0;padding-bottom:1rem}.overlay{height:0%;width:100%;position:fixed;z-index:200;top:0;left:0;background-color:#E8777C;overflow-x:hidden;display:flex;align-items:flex-start;justify-content:center;align-self:center;flex-direction:column;overflow:hidden}.overlayContent{margin-left:17vw;margin-right:17vw;margin-right-overflow:hidden;margin-right-height:80px}.overlayContent p:nth-of-type(1){font-family:'Roboto mono';color:black;opacity:0;font-size:1rem;margin:0;padding-bottom:2rem}#contact{display:flex;align-items:center;justify-content:flex-start;flex-wrap:nowrap}#contact img{padding-right:1rem;height:1.5rem;width:auto}#contact p{opacity:1;font-family:'dinAlt';font-size:1.14rem;color:white;position:relative;top:0.9rem}.projects{all:unset;color:black;font-size:1.1rem;position:absolute;top:1.3rem;left:1rem}#contact{cursor:pointer}.overflowHidden{overflow:hidden}@media (max-width: 992px){.header{font-size:3rem}.overlayContent p:nth-of-type(1){font-size:1rem}}@media (max-width: 768px){.header{font-size:2.5rem}.overlayContent p:nth-of-type(1){font-size:0.9rem}#contact img{height:1rem;width:auto}#contact p{font-size:1rem}}@media (max-width: 576px){.header{font-size:2.2rem}.overlayContent p:nth-of-type(1){font-size:0.8rem}#contact img{height:1rem;width:auto}#contact p{font-size:1rem}}
